import './Model.css';
import {DataInput, DataInputVertical} from './../DataInput/DataInput.js';

function Model(props){
    return(
        <div className="model">
            <div className="model-resistor"
                style={{
                    height: `${80+(30/1000)*props.R}px`
                }}
            ></div>
            <div className="model-capacity"
                style={{
                    width: `${25+(20/1000)*props.C}px`
                }}
            >
                <div className="model-capacity__item"></div>
                <div className="model-capacity__item"></div>
            </div>
            <div className="model-eds">E</div>
            <svg>
                <path
                    stroke="black"
                    style={{
                        strokeWidth: 1+(5/1000)*props.L
                    }}                
                d="M 126.63867 158.98828 A 36.47 36.47 0 0 0 90.289062 191 L 90.308594 195 L 94 195 A 32.5 32.5 0 0 1 159 195 L 162 195 A 32.5 32.5 0 0 1 227 195 L 230 195 A 32.5 32.5 0 0 1 295 195 L 298 195 A 32.5 32.5 0 0 1 363 195 L 366.5918 195 L 366.58984 194.99414 L 366.71094 191 L 366.18359 187.87109 A 36.47 36.47 0 0 0 296.5 182.30078 A 36.46 36.46 0 0 0 228.5 182.30078 A 36.46 36.46 0 0 0 160.5 182.30078 A 36.47 36.47 0 0 0 126.63867 158.98828 z " id="capacity-svg" />
                <path id="path" d="m 462.00003,4 v 187 h -95.29 a 36.47,36.47 0 0 0 -70.21,-8.7 36.46,36.46 0 0 0 -68,0 36.46,36.46 0 0 0 -68.00003,0 36.47,36.47 0 0 0 -70.21,8.7 H 4 V 4 h 458.00003 m 4,-4 H 0 v 195 h 94 a 32.5,32.5 0 0 1 65,0 h 3.00003 a 32.5,32.5 0 0 1 65,0 h 3 a 32.5,32.5 0 0 1 65,0 h 3 a 32.5,32.5 0 0 1 65,0 h 103 z"/>
            </svg>

            <DataInput value={props.C} handler={(data) => {props.handler(data, props.R, props.V, props.L)}} style={{top: 0}} unit="мкФ" />
            <DataInputVertical value={props.R} handler={(data) => {props.handler(props.C, data, props.V, props.L)}} style={{left: 0}} unit="Ом"/>
            <DataInputVertical value={props.V} handler={(data) => {props.handler(props.C, props.R, data, props.L)}} style={{right: 0, flexDirection: 'row-reverse'}} unit="Гц" textStyle={{transform: "rotate(0deg)"}} />
            <DataInput value={props.L} handler={(data) => {props.handler(props.C, props.R, props.V, data)}} style={{bottom: 0}} unit="мГн"/>
            
        </div>
    );
}

export default Model;