import React, { useState } from 'react';
import './Page.css';

function Page(props){
    return(
        <div className={"page "+props.className+" "+(props.active ? "active" : "")} style={props.style} onClick={props.onClick}>
            <div className="page-name"
                style={{
                    color: props.whiteMode ? '#fff' : '#000'
                }}
            >{props.name}</div>
            {props.children}
        </div>
    );
}

export {Page};