import React from 'react';

import Slider from 'react-rangeslider';
import 'react-rangeslider/lib/index.css';

import './DataInput.css';

function DataInput(props){
    return(
        <div class="data-input" style={props.style}>
            <div className="data-input__input">
                <div className="data-input__input__value">{props.value+" "+props.unit}</div>
            </div>
            <Slider
                min={5}
                max={1000}
                value={props.value}
                orientation="horizontal"
                onChange={props.handler}
            />
        </div>
    );
}

function DataInputVertical(props){
    return(
        <div className="data-input vertical" style={props.style}>
            <div className="data-input__input">
                <div className="data-input__input__value" style={{...props.textStyle}}>{props.value+" "+props.unit}</div>
            </div>
            <Slider
                min={5}
                max={1000}
                value={props.value}
                orientation="vertical"
                onChange={props.handler}
            />
        </div>
    );
}

export {DataInput, DataInputVertical};