import React from 'react';
import './App.css';


import Model from './components/Model/Model.js';
import Graph from './components/Graph/Graph.js';
import {Page} from './components/Page/Page.js';
import {Info} from './components/Info/Info.js';

import {TIME, STEP, E0} from './constants.js';

class App extends React.Component{
    constructor(){
        super();

        this.state = {
            mode: 'path',
            C: 53,
            R: 348,
            V: 892,
            L: 184,
            I: 4
        };
    }

    _setData(C, R, V, L){
        this.setState({
            C,
            R,
            V,
            L
        });
    }

    _getR(){
        return this.state.R;
    }

    _getL(){
        return this.state.L*Math.pow(10, -3);
    }

    _getC(){
        return this.state.C*Math.pow(10, -6);
    }

    _getV(){
        return this.state.V;
    }


    _calcE(t){
        return E0*Math.cos(this.state.V*t);
    }

    _calcB(){
        return this.state.R/(2*this._getL());
    }

    _calcVRes(){
        return (1/(2*Math.PI*Math.sqrt(this._getL()*this._getC())));
    }

    _calcVRres(){
        return 1/Math.sqrt(this._getL()*this._getC());
    }

    _calcVCres(){
        //console.log(this._calcVRres()*Math.sqrt(1+2*Math.pow(this._calcB()/this._calcVRres(), 2)));
        return this._calcVRres()*Math.sqrt(1+2*Math.pow(this._calcB()/this._calcVRres(), 2));
    }

    _calcVLres(){
        return this._calcVRes()/this._calcVCres();
    }

    _calcRFull(){
        return 1;//Math.sqrt(Math.pow(this.state.R, 2) + Math.pow(this.state.V*this.state.L*Math.pow(10, -3)-(1/(this.state.V*this.state.C*Math.pow(10, -6))), 2)).toFixed(2);
    }

    _calcFi(){
        return Math.atan((this.state.V*this.state.L*Math.pow(10, -3)-(1/(this.state.V*this.state.C*Math.pow(10, -6))))/this.state.R);
    }

    _calcI0(){
        return E0/Math.sqrt(Math.pow(this.state.R, 2) + Math.pow(this.state.V*this.state.L*Math.pow(10, -3) - (1/(this.state.V*this.state.C*Math.pow(10, -6))), 2));     
    }

    _calcI(t){
        return this._calcI0()*Math.cos(this.state.V*t);
    }

    _calcUR(t){
        return this._calcI0()*this.state.R*Math.cos(this.state.V*t-this._calcFi());
    }

    _calcUL(t){
        //мм
        return this.state.V*(this.state.L*Math.pow(10, -3))*this._calcI0()*Math.cos(this.state.V*t+Math.PI/2-this._calcFi()); 
    }

    _calcUC(t){
        //нано
        return (this._calcI0()/(this.state.V*this.state.C*Math.pow(10, -6)))*Math.cos(this.state.V*t-Math.PI/2-this._calcFi());
    }

    _calcEdsData(){
        var arrX = [];
        var arrY = [];
        for(var i = 0; i < TIME; i += STEP){
            arrX.push(i.toFixed(2));
            arrY.push(this._calcE(i));
        }

        return {
            name: "EDS",
            color: "#2ecc71",
            x: arrX,
            y: arrY
        };
    }

    _calcURData(){
        var arrX = [];
        var arrY = [];

        for(var i = 0; i < TIME; i += STEP){
            arrX.push(i.toFixed(2));
            arrY.push(this._calcUR(i));
        }

        return {
            name: "UR",
            color: "#3498db",
            x: arrX,
            y: arrY
        };
    }

    _calcULData(){
        var arrX = [];
        var arrY = [];

        for(var i = 0; i < TIME; i += STEP){
            arrX.push(i.toFixed(2));
            arrY.push(this._calcUL(i));
        }

        return {
            name: "UL",
            color: "#e74c3c",
            x: arrX,
            y: arrY
        };
    }

    _calcUCData(){
        var arrX = [];
        var arrY = [];

        for(var i = 0; i < TIME; i += STEP){
            arrX.push(i.toFixed(2));
            arrY.push(this._calcUC(i));
        }

        return {
            name: "UC",
            color: "#9b59b6",
            x: arrX,
            y: arrY
        };
    }

    _calcIData(){
        var arrX = [];
        var arrY = [];

        for(var i = 0; i < TIME; i += STEP){
            arrX.push(i.toFixed(2));
            arrY.push(this._calcI(i));
        }

        return {
            name: "I",
            color: "#1abc9c",
            x: arrX,
            y: arrY
        };
    }

    _onPathClick(){
        this.setState({
            mode: 'path'
        });
    }

    _onGraphClick(){
        this.setState({
            mode: 'graph'
        });
    }

    render(){
        var pageModelStyle = {
            marginLeft: this.state.mode === 'graph' ? '-73vw' : '10px'
        };

        var appStyle = {
            backgroundColor: this.state.mode === 'graph' ? '#000' : 'transparent'
        }

        var edsData = this._calcEdsData();
        var urData = this._calcURData();
        var ulData = this._calcULData();
        var ucData = this._calcUCData();
        var IData = this._calcIData();

        return(
            <div className="App" style={appStyle}>
                <Page className="page-model" name="3.11" style={pageModelStyle} onClick={this._onPathClick.bind(this)} active={this.state.mode === 'path'}>
                    <Model C={this.state.C} R={this.state.R} V={this.state.V} L={this.state.L} handler={this._setData.bind(this)} />
                    <Info
                        X={this._calcRFull()}
                        V={this._calcVRes()}

                        Ur={(Math.max.apply(null, urData.y)*1000).toFixed(2)}
                        Ul={(Math.max.apply(null, ulData.y)*1000).toFixed(2)}
                        Uc={(Math.max.apply(null, ucData.y)*1000).toFixed(2)}

                        VRres={this._calcVRres().toFixed(0)}
                        VCres={this._calcVRes().toFixed(0)}
                    />
                </Page>
                <Page className="page-graph" name="Графики" onClick={this._onGraphClick.bind(this)} active={this.state.mode === 'graph'} whiteMode={ this.state.mode === 'graph'}>
                    <Graph data={[edsData]} />
                    <Graph data={[urData, ulData, ucData, IData]} />
                </Page>
            </div>
        );
    }
}

export default App;
