import React from 'react';
import './Info.css';

function InfoItem(props){
    return(
        <div className="info-item" title={props.title}>
            <div className="info-item__name">
                <div>{props.name}</div>
                <div className="info-item__name__index">{props.index}</div>
            </div>
            <div className="info-item__data">{props.data}</div>
            <div className="info-item__unit">{props.unit}</div>
        </div>
    );
}

function Info(props){
    return(
        <div className="info">
            {/* <InfoItem name={"X"} index={"e"} data={props.X} unit={"Ом"}/> */}
            <InfoItem title="Напряжение на индуктивности" name={"U"} index={"L"} data={props.Ul} unit={"мВ"} />
            <InfoItem title="Напряжение на сопротивлении" name={"U"} index={"R"} data={props.Ur} unit={"мВ"} />
            <InfoItem title="Напряжение на конденсаторе" name={"U"} index={"C"} data={props.Uc} unit={"мВ"} />
            <InfoItem title="Резонансная частота" name={"Ω"} index={"R res"} data={props.VRres} unit={"Гц"}/>
            <InfoItem title="Резонансная частота по формуле Томсона" name={"Ω"} index={"C res"} data={props.VCres} unit={"Гц"}/>
        </div>
    );
}

export {Info};